import React from 'react';
import { Link } from 'react-router-dom';
import { ModalRoute } from 'react-router-modal';
import { Example, ExampleSource, ExampleBody, ExampleResult, ExampleExplanation} from '../example_layout';
import './index.css';

export default function TransitionsExample({ match }) {
  return (
    <Example>
      <h5>Transitions</h5>
      <ExampleExplanation>
        <p>
          Transition animations use CSS classes that are applied just after a modal is rendered for the first time,
          and just before it is removed from the DOM.
        </p>
        <p>
          CSS is used to apply the transitioning properties.
        </p>
        <p>
          The following properties on ModalContainer control transitional css classes for all modals:
          <ul>
            <li>modalInClassName, modalOutClassName</li>
            <li>backdropInClassName, backdroupOutClassName</li>
            <li>outDelay</li>
          </ul>
        </p>
        <p>
          The following properties on Modal, ModalLink, and ModalRoute override the ModalContainer settings:
          <ul>
            <li>inClassName, outClassName</li>
            <li>backdropInClassName, backdroupOutClassName</li>
            <li>outDelay</li>
          </ul>
        </p>
        <p>
          The lifecycle is as follows:
          <ul>
            <li>Modal is rendered with className only</li>
            <li>After a very brief delay, inClassName is added</li>
            <li>Modal is about to unmount, inClassName is removed and outClassName is added</li>
            <li>After outDelay milliseconds, Modal is unmounted</li>
          </ul>
        </p>
      </ExampleExplanation>
      <ExampleBody>
        <ExampleResult>
          <h5>Example</h5>
          <p>Here's an example that sets different transition timings for the backdrop and the modal content</p>
          <p>The backdrop fades in in 200ms and fades out for 0.3 seconds</p>
          <p>The modal fades in in 500ms and fades out in 1.5 seconds</p>
          <p>The outDelay prop ensures that the modal and backdrop remain in the DOM until the transition is complete</p>
          <p>Note: Don't use a 1.5 second transition in real life. It's annoying.</p>

          <Link to={`${match.url}/slow-fade-out`}>Show modal with slow fade-out</Link>
          <ModalRoute
            path={`${match.url}/slow-fade-out`}
            parentPath={match.url}
            className='example-modal'
            inClassName='example-modal-in'
            outClassName='example-modal-out'
            backdropClassName='example-backdrop'
            backdropInClassName='example-backdrop-in'
            backdropOutClassName='example-backdrop-out'
            outDelay={1500}
          >
            Slow fade out
          </ModalRoute>
        </ExampleResult>
        <ExampleSource>{`
  CSS:
    .example-modal {
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);
      background-color: white;
      border-radius: 5px;
      border: 1px solid #777;
      padding: 2rem;
      min-width: 20rem;
      max-width: 60rem;
      width: 90%;
      opacity: 0;
    }

    .example-modal-in  {
      transition: opacity 0.5s ease-in;
      opacity: 1 
    }

    .example-modal-out {
      transition: opacity 0.3s ease-out;
      opacity: 0
    }

    .example-backdrop {
      background-color: black;
      opacity: 0;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    .example-backdrop-in  {
      transition: opacity 0.3s ease-in;
      opacity: 0.5;
    }

    .example-backdrop-out  {
      transition: opacity 1.5s ease-out;
      opacity: 0;
    }

  Javascript:
    <Link to={\`\${match.url}/slow-fade-out\`}>Show modal with slow fade out</Link>
    <ModalRoute
      path={\`\${match.url}/slow-fade-out\`}
      parentPath={match.url}
      className='example-modal'
      inClassName='example-modal-in'
      outClassName='example-modal-out'
      backdropClassName='example-backdrop'
      backdropInClassName='example-backdrop-in'
      backdropOutClassName='example-backdrop-out'
      outDelay={1500}
    >
      Slow fade out
    </ModalRoute>
        `} </ExampleSource>
      </ExampleBody>
    </Example>

  );

}
