import React from 'react';
import './example_layout.css';

export const Example = ({children}) => (
  <div className='example'>{children}</div>
);
export const ExampleSource = ({children}) => (
  <pre><code>{children}</code></pre>
);

export const ExampleBody = ({children}) => (
  <div className='example__body'>{children}</div>
);

export const ExampleResult = ({children}) => (
  <div className='example__result'>{children}</div>
);

export const ExampleExplanation = ({children}) => (
  <div className='example__explanation'>{children}</div>
);
